@import "../../style.scss";

.register {
  height: 100vh;
  background-color: rgb(193, 190, 255);
  display: flex;
  align-items: center;
  justify-content: center;

  .card {
    width: 70%;
    display: flex;
    flex-direction: row-reverse;
    background-color: white;
    border-radius: 10px;
    min-height: 600px;
    overflow: hidden;

    @include mobile {
      width: 80%;
      flex-direction: column;
    }

    .left {
      flex: 1;
      background: linear-gradient(rgba(39, 11, 96, 0.5), rgba(39, 11, 96, 0.5)),
        url("https://cdn.pixabay.com/photo/2020/10/28/10/02/painting-5692936_960_720.jpg")
          center;
      background-size: cover;
      padding: 50px;
      display: flex;
      flex-direction: column;
      gap: 30px;
      color: white;

      @include mobile {
        gap: 5px;
        padding: 20px;
      }

      h1 {
        font-size: 100px;
        line-height: 100px;

        @include mobile {
          font-size: 60px;
          line-height: 1;
          width: 10px;
          margin: 10px;
          justify-content: center;
        }
      }

      p {
      }

      span {
        font-size: 14px;
      }

      button {
        width: 50%;
        padding: 10px;
        border: none;
        background-color: white;
        color: rebeccapurple;
        font-weight: bold;
        cursor: pointer;
      }
    }

    .right {
      flex: 1;
      padding: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 50px;

      @include mobile {
        gap: 0;
        padding: 0px 50px;
      }

      h1 {
        color: #555;

        @include mobile {
          margin-bottom: 5px;
        }
      }

      form {
        display: flex;
        flex-direction: column;
        gap: 30px;
        input {
          border: none;
          border-bottom: 1px solid lightgrey;
          padding: 20px 10px;

          @include mobile {
            padding: 5px;
            margin: 0px 5px;
          }
        }
        button {
          width: 50%;
          padding: 10px;
          border: none;
          background-color: #938eef;
          color: white;
          font-weight: bold;
          cursor: pointer;

          @include mobile {
            margin: 10px;
          }
        }
      }
    }
  }
}
