@import "../../style.scss";

.leftBar {
  @include themify($themes) {
    flex: 2;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    background-color: themed("bg");
    color: themed("textColor");
    overflow: scroll;
    width: 250px;

    @include mobile {
      display: none;
    }

    &::-webkit-scrollbar {
      display: none;
    }
    hr {
      margin: 20px 0px;
      border: none;
      height: 0.5px;
      background-color: themed("border");
    }
    .container {
      padding: 20px;
      .menu {
        display: flex;
        flex-direction: column;
        gap: 20px;

        span {
          font-size: 12px;
        }

        .user {
          display: flex;
          align-items: center;
          gap: 10px;
          img {
            width: 40px;
            height: 40px;
            border-radius: 50px;
            object-fit: cover;
          }

          span {
            font-size: 14px;
          }
        }

        .item {
          display: flex;
          align-items: center;
          gap: 10px;
        }

        img {
          width: 30px;
        }

        span {
          font-size: 14px;
        }
      }
    }
  }
}
