@import "../../style.scss";

.post {
  @include themify($themes) {
    -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
    -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
    box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
    border-radius: 20px;
    background-color: themed("bg");
    color: themed("textColor");
    .container {
      padding: 30px;
    }

    .user {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .userInfo {
        display: flex;
        gap: 20px;
        img {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          object-fit: cover;
        }

        .details {
          display: flex;
          flex-direction: column;

          .name {
            font-weight: 500;
          }

          .date {
            font-size: 12px;
          }
        }
      }

      .buttonContainer {
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-gap: 10px;

        .deleteButton {
          background-color: #f0544f;
          color: white;
          border: none;
          padding: 5px;
          cursor: pointer;
          border-radius: 5px;
        }

        .cancelButton {
          display: flex;
          padding: 5px;
          border-radius: 5px;
          border: none;
          color: white;
          background-color: #5271ff;
          cursor: pointer;
        }
      }
    }

    .content {
      margin: 20px 0px;

      img {
        width: 100%;
        max-height: 500px;
        object-fit: cover;
        margin-top: 20px;
      }
    }

    .info {
      display: flex;
      align-items: center;
      gap: 20px;

      .item {
        display: flex;
        align-self: center;
        gap: 10px;
        cursor: pointer;
        font-size: 14px;
      }
    }
  }
}
